import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-galerias-detail',
  templateUrl: './galerias-detail.component.html',
  styleUrls: ['./galerias-detail.component.scss']
})
export class GaleriasDetailComponent implements OnInit {

  private loader: boolean = false;
  private gallery: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<GaleriasDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getGaleriaDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.gallery = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar la galera.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  deletePicture(i: string, url: string) {
    this.loader = true;
    this.back.deleteGalleryPicture(this.data.id, url).subscribe(
      data => {
        this.loader = false;
        this.gallery.pictures.splice(i, 1);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo eliminar la imgen de la galera.', '', {duration: 4000});
      }
    );
  }

}



