import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';
import { SegDetailComponent } from 'app/seg-detail/seg-detail.component';

@Component({
  selector: 'app-pacientes-detail',
  templateUrl: './pacientes-detail.component.html',
  styleUrls: ['./pacientes-detail.component.scss']
})
export class PacientesDetailComponent implements OnInit {

  private loader: boolean = false;
  private user: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<PacientesDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getPacienteDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.user = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el paciente.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  openSeg(id: string, seg: string) {
    const modal = this.dialog.open(SegDetailComponent, {data: {id, seg}, width: '50%'})
  }

  getKeys(obj) {
    return Object.keys(obj).length
  }

}



