import { ImageResult } from 'ng2-imageupload';
import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, Inject } from '@angular/core';

@Component({
  selector: 'app-profile-curriculum-upload',
  templateUrl: './profile-curriculum-upload.component.html',
  styleUrls: ['./profile-curriculum-upload.component.scss']
})
export class ProfileCurriculumUploadComponent {

  private loader: boolean = false;
  private pdf: string;

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<ProfileCurriculumUploadComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  selected(res: ImageResult) {
    this.loader = true;
    this.back.uploadCurriculum(res.file).subscribe(
      data => {
        this.loader = false;
        this.pdf = data.url;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo subir la imgen', '', {duration: 4000});
      }
    );
  }

  upload(url: string) {
    let curri = '/pdf/' + url;
    this.loader = true;
    this.back.updateCurriculum(curri).subscribe(
      data => {
        this.loader = false;
        this.ref.close(curri);
        this.back.user.curriculumn = curri;
        this.back.setAuth(this.back.user, this.back.token);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo actualizar el curriculum', '', {duration: 4000});
      }
    );
  }

}



