import { EventosDetailComponent } from './../eventos-detail/eventos-detail.component';
import { EventosAddComponent } from './../eventos-add/eventos-add.component';
import { EventosDeleteComponent } from './../eventos-delete/eventos-delete.component';
import { EventosEditComponent } from './../eventos-edit/eventos-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-eventos',
  templateUrl: './eventos.component.html',
  styleUrls: ['./eventos.component.scss']
})
export class EventosComponent implements OnInit {

  private loader: boolean = false;
  private allEventos: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllEvents().subscribe(
      data => {
        this.loader = false;
        this.allEventos = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los eventos.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(EventosEditComponent, {data: id, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(EventosDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allEventos.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(EventosAddComponent, {width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allEventos.push(data);
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(EventosDetailComponent, {width: '80%', data: {id}});
  }

}



