import { Component, Inject } from '@angular/core';
import { MD_DIALOG_DATA, MdDialogRef, MdSnackBar } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-citas-delete',
  templateUrl: './citas-delete.component.html',
  styleUrls: ['./citas-delete.component.scss']
})
export class CitasDeleteComponent {

   private loader: boolean = false;

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CitasDeleteComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  delete(id: string) {
    this.loader = true;
    this.back.deleteCita(id).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Cita eliminada correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo eliminar la cita.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



