import { Component, Inject } from '@angular/core';
import { MD_DIALOG_DATA, MdDialogRef, MdSnackBar } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-eventos-delete',
  templateUrl: './eventos-delete.component.html',
  styleUrls: ['./eventos-delete.component.scss']
})
export class EventosDeleteComponent {

  private loader: boolean = false;

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<EventosDeleteComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  delete(id: string) {
    this.loader = true;
    this.back.deleteEvent(id).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Evento eliminado correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo eliminar el evento.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



