import { ImageResult } from 'ng2-imageupload';
import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, Inject } from '@angular/core';

@Component({
  selector: 'app-galerias-upload',
  templateUrl: './galerias-upload.component.html',
  styleUrls: ['./galerias-upload.component.scss']
})
export class GaleriasUploadComponent {

  private loader: boolean = false;
  private addData: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<GaleriasUploadComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  selected(res: ImageResult) {
    this.loader = true;
    this.back.uploadFile(res.file).subscribe(
      data => {
        this.loader = false;
        this.addData.img = data.secure_url;
        this.loader = false;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo subir la imgen', '', {duration: 4000});
      }
    );
  }

  upload(data: any) {
    this.loader = true;
    this.back.updateGalleryPictures(this.data.id, data.img).subscribe(
      data => {
        this.loader = false;
        this.ref.close(data);
        console.log('====================================');
        console.log(data);
        console.log('====================================');
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar la imgen', '', {duration: 4000});
      }
    );
  }

}



