import { EntradasDetailComponent } from './../entradas-detail/entradas-detail.component';
import { EntradasAddComponent } from './../entradas-add/entradas-add.component';
import { EntradasDeleteComponent } from './../entradas-delete/entradas-delete.component';
import { EntradasEditComponent } from './../entradas-edit/entradas-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-entradas',
  templateUrl: './entradas.component.html',
  styleUrls: ['./entradas.component.scss']
})
export class EntradasComponent implements OnInit {

  private loader: boolean = false;
  private allEntradas: any[] = [];
  private id: string = JSON.parse(localStorage.getItem('user')).id

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    if (!localStorage.getItem('user')) return
    if (JSON.parse(localStorage.getItem('user')).roles.indexOf('ROLE_ADMIN') >= 0) {
      this.back.getAllEntradas().subscribe(data => {
        this.loader = false
        this.allEntradas = data
      }, err => {
        this.loader = false
        this.snack.open('Error, no se pudieron cargar todas las entradas.', '', {duration: 4000});
      })
    } else {
      this.back.getMyEntradas(this.id).subscribe(
        data => {
          this.loader = false;
          this.allEntradas = data;
        },
        err => {
          this.loader = false;
          this.snack.open('Error, no se pudieron cargar mis entradas.', '', {duration: 4000});
        }
      );
    }
    
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(EntradasEditComponent, {data: id, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(EntradasDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allEntradas.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(EntradasAddComponent, {width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allEntradas.push(data);
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(EntradasDetailComponent, {width: '80%', data: {id}});
  }

}



