import { Router } from '@angular/router';
import { BackendService } from './../../../backend.service';
import { Component } from '@angular/core';
import { MdSnackBar } from "@angular/material";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent {

  private loader: boolean = false;
  private username: string = '';
  private password: string = '';

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private router: Router
  ) { }

  login(username: string, password: string) {
    this.loader = true;
    this.back.login(username, password).subscribe(
      data => {
        this.loader = false;
        if(data.err) return this.snack.open(data.err, '', {duration: 4000});
        this.snack.open('Sesin iniciada correctamente.', '', {duration: 4000});
        this.back.setAuth(data.user, data.token);
        this.router.navigate(['/home', {outlets: {inner: 'dashboard'}}]);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo iniciar sesin.', '', {duration: 4000});
      }
    );
  }

}



