import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-pacientes-edit',
  templateUrl: './pacientes-edit.component.html',
  styleUrls: ['./pacientes-edit.component.scss']
})
export class PacientesEditComponent implements OnInit {

  private loader: boolean = false;
  private editData: any = {};
  private allUsers: any[] = [];

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<PacientesEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getPacienteDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.editData = data;
        this.editData.terapeuta = data.terapeuta.id;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el paciente.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
    this.back.getAllUsers().subscribe(
      data => {
        this.loader = false;
        this.allUsers = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los usuarios', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editPaciente(this.data.id, data).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Paciente editado correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar el paciente.', '', {duration: 4000});
      }
    );
  }

}



