import { CitasAdminComponent } from './pages/admin/citas-admin/citas-admin.component';
import { CitasArchivadasComponent } from './pages/terapeuta/citas-archivadas/citas-archivadas.component';
import { CitasComponent } from './pages/terapeuta/citas/citas.component';
import { PacientesTerapeutaComponent } from './pages/terapeuta/pacientes-terapeuta/pacientes-terapeuta.component';
import { PacientesComponent } from './pages/core/pacientes/pacientes.component';
import { ProfileComponent } from './pages/core/profile/profile.component';
import { GaleriasComponent } from './pages/admin/galerias/galerias.component';
import { EntradasComponent } from './pages/admin/entradas/entradas.component';
import { EventosComponent } from './pages/admin/eventos/eventos.component';
import { UsuariosComponent } from './pages/admin/usuarios/usuarios.component';
import { DashboardComponent } from './pages/core/dashboard/dashboard.component';
import { HomeComponent } from './pages/core/home/home.component';
import { LoginComponent } from './pages/core/login/login.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PublicProfileComponent } from './pages/admin/public-profile/public-profile.component';

const routes: Routes = [
  {path: 'login', component: LoginComponent},
  {path: 'home', component: HomeComponent, children: [
    {path: '', redirectTo: '/dashboard', pathMatch: 'full'},
    {path: 'dashboard', component: DashboardComponent, outlet: 'inner'},
    {path: 'usuarios', component: UsuariosComponent, outlet: 'inner'},
    {path: 'perfiles-publicos', component: PublicProfileComponent, outlet: 'inner'},
    {path: 'eventos', component: EventosComponent, outlet: 'inner'},
    {path: 'blog', component: EntradasComponent, outlet: 'inner'},
    {path: 'galerias', component: GaleriasComponent, outlet: 'inner'},
    {path: 'perfil', component: ProfileComponent, outlet: 'inner'},
    {path: 'pacientes', component: PacientesComponent, outlet: 'inner'},
    {path: 'mis-pacientes', component: PacientesTerapeutaComponent, outlet: 'inner'},
    {path: 'citas', component: CitasComponent, outlet: 'inner'},
    {path: 'citas-admin', component: CitasAdminComponent, outlet: 'inner'},
    {path: 'citas-archivadas', component: CitasArchivadasComponent, outlet: 'inner'},
  ]},
  {path: '**', component: LoginComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



