import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-eventos-detail',
  templateUrl: './eventos-detail.component.html',
  styleUrls: ['./eventos-detail.component.scss']
})
export class EventosDetailComponent implements OnInit {

  private loader: boolean = false;
  private event: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<EventosDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getEventDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.event = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el evento.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



