import { MdDialogRef, MdSnackBar } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component } from '@angular/core';

@Component({
  selector: 'app-profile-password',
  templateUrl: './profile-password.component.html',
  styleUrls: ['./profile-password.component.scss']
})
export class ProfilePasswordComponent {

  private loader: boolean = false;

  constructor(
    private back: BackendService,
    private ref: MdDialogRef<ProfilePasswordComponent>,
    private snack: MdSnackBar
  ) { }

  changePassword(current: string, newPass: string, confirmation: string) {
    if(newPass != confirmation) return this.snack.open('Las contraseas nuevas no coinciden.', '', {duration: 4000});
    this.back.changePassword(current, newPass).subscribe(
      data => {
        this.loader = false;
        if(data.err) return this.snack.open(data.err, '', {duration: 4000});
        this.snack.open('Contrasea cambiada correctamente', '', {duration: 4000});
        // TODO: Cerrar sesin despus de cambiar la contrasea
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cambiar la contrasea', '', {duration: 4000});
      }
    );
  }

}



