import { ImageResult } from 'ng2-imageupload';
import { BackendService } from './../../../backend.service';
import { MdDialogRef, MdSnackBar } from '@angular/material';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-entradas-add',
  templateUrl: './entradas-add.component.html',
  styleUrls: ['./entradas-add.component.scss']
})
export class EntradasAddComponent {

  private loader: boolean = false;
  private addData: any = {};

  constructor(
    private ref: MdDialogRef<EntradasAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  add(data: any) {
    this.loader = true;
    data.user = this.back.user.id;
    this.back.addEntrada(data).subscribe(
      res => {
        this.loader = false;
        this.snack.open('Entrada agregada correctamente', '', {duration: 4000});
        this.ref.close(res);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar la entrada', '', {duration: 4000});
        this.ref.close({err});
      }
    );    
  }

  selected(res: ImageResult) {
    this.loader = true;
    this.back.uploadFile(res.file).subscribe(
      data => {
        this.loader = false;
        this.addData.img = data.secure_url;
        this.loader = false;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo subir la imgen', '', {duration: 4000});
      }
    );
  }

}



