import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-citas-archivadas',
  templateUrl: './citas-archivadas.component.html',
  styleUrls: ['./citas-archivadas.component.scss']
})
export class CitasArchivadasComponent implements OnInit {

  private loader: boolean = false;
  private allCitas: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllCitasArchivadas().subscribe(
      data => {
        this.loader = false;
        this.allCitas = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todas las citas.', '', {duration: 4000});
      }
    );
  }
  
  openDelete(id: string, i: number) {
    this.loader = true
    this.back.deleteCita(id).subscribe(data => {
      this.ngOnInit()
      this.snack.open('Cita eliminada correctamente...', '', {duration: 4000});
      this.loader = false
    }, err => {
      this.loader = false
      this.snack.open('Error, no se pudo eliminar la cita.', '', {duration: 4000});
    })
  }

}



