import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-eventos-edit',
  templateUrl: './eventos-edit.component.html',
  styleUrls: ['./eventos-edit.component.scss']
})
export class EventosEditComponent implements OnInit {

  private loader: boolean = false;
  private editData: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<EventosEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getEventDetail(this.data).subscribe(
      data => {
        this.loader = false;
        this.editData = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el evento.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editEvent(this.data, data).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Evento editado correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar el evento.', '', {duration: 4000});
      }
    );
  }

}



