import { CitasArchivadasComponent } from './../../terapeuta/citas-archivadas/citas-archivadas.component';
import { CitasDetailComponent } from './../../terapeuta/citas-detail/citas-detail.component';
import { CitasAddComponent } from './../../terapeuta/citas-add/citas-add.component';
import { CitasDeleteComponent } from './../../terapeuta/citas-delete/citas-delete.component';
import { CitasEditComponent } from './../../terapeuta/citas-edit/citas-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-citas-admin',
  templateUrl: './citas-admin.component.html',
  styleUrls: ['./citas-admin.component.scss']
})
export class CitasAdminComponent implements OnInit {

  private loader: boolean = false;
  private allCitas: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllCitasNoArchivadas().subscribe(
      data => {
        this.loader = false;
        this.allCitas = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todas las citas.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(CitasEditComponent, {data: {id, isOwn: false}, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(CitasDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allCitas.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(CitasAddComponent, {width: '80%', data: {isOwn: false}});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allCitas.push(data);
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(CitasDetailComponent, {width: '80%', data: {id}});
  }

  openArchive(id: string) {
    let modal = this.dialog.open(CitasArchivadasComponent, {width: '80%', data: {id}});
    modal.afterClosed().subscribe(data => this.ngOnInit());
  }

}



