import { ProfilePasswordComponent } from './../profile-password/profile-password.component';
import { ProfileEditComponent } from './../profile-edit/profile-edit.component';
import { ProfileCurriculumUploadComponent } from './../profile-curriculum-upload/profile-curriculum-upload.component';
import { MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component } from '@angular/core';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent {

  private user: any;

  constructor(
    private back: BackendService,
    private dialog: MdDialog
  ) { this.user = this.back.user; }

  getStyledPoblation(pob: string[]) {
    if(!pob) return 'Poblacin no asignada';
    return pob.map((s) => {
      if(s == 'POB_NINOS') return 'Nios';
      if(s == 'POB_ADO') return 'Adolescente';
      if(s == 'POB_ADULTOS') return 'Adultos';
      if(s == 'POB_ADULTOS_PLUS') return 'Adultos Mayores';
      if(s == 'POB_FAM') return 'Familia';
      if(s == 'POB_PAREJA') return 'Pareja';
    }).join(', ');
  }

  openUploadCurriculum() {
    let modal = this.dialog.open(ProfileCurriculumUploadComponent, {width: '50%'});
    modal.afterClosed().subscribe((data) => this.user.curriculum = data);
  }

  openEdit() {
    let modal = this.dialog.open(ProfileEditComponent, {width: '60%'});
    modal.afterClosed().subscribe((data) => {
      if(!data) return;
      this.back.setAuth(data, this.back.token);
      this.user = data;
    });
  }

  openChangePassword() {
    let modal = this.dialog.open(ProfilePasswordComponent, {width: '50%'});
  }

}



