import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-usuarios-edit',
  templateUrl: './usuarios-edit.component.html',
  styleUrls: ['./usuarios-edit.component.scss']
})
export class UsuariosEditComponent implements OnInit {

  private loader: boolean = false;
  private editData: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<UsuariosEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getUserDetail(this.data).subscribe(
      data => {
        this.loader = false;
        this.editData = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el usuario.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editUser(this.data, data).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Usuario editado correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar el usuario.', '', {duration: 4000});
      }
    );
  }

  getStyledRoles(roles: string[]) {
    if(!roles) return 'Roles no asignados';
    if(roles.length <= 0) return 'Roles no asignados';
    return roles.map(
      (role) => {
        if(role == 'ROLE_ADMIN') return 'Administrador';
        if(role == 'ROLE_FOODTRUCKERO') return 'Foodtruckero';
      }
    ).join(', ');
  }

}



