import { CuestionarioEditComponent } from './../cuestionario-edit/cuestionario-edit.component';
import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-cuestionario-detail',
  templateUrl: './cuestionario-detail.component.html',
  styleUrls: ['./cuestionario-detail.component.scss']
})
export class CuestionarioDetailComponent implements OnInit {

  private loader: boolean = false;
  private c: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CuestionarioDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getPacienteDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.c = data.cuestionario;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el cuestionario.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  openEdit(id: string) {
    this.ref.close();
    let modal = this.dialog.open(CuestionarioEditComponent, {width: '80%', data: {id}});
  }

}



