import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-entradas-edit',
  templateUrl: './entradas-edit.component.html',
  styleUrls: ['./entradas-edit.component.scss']
})
export class EntradasEditComponent implements OnInit {

  private loader: boolean = false;
  private editData: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<EntradasEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getEntradaDetail(this.data).subscribe(
      data => {
        this.loader = false;
        this.editData = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar la entrada.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editEntrada(this.data, data).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Entrada editada correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar la entrada.', '', {duration: 4000});
      }
    );
  }

}



