import { CitasArchivarComponent } from './../citas-archivar/citas-archivar.component';
import { CitasDetailComponent } from './../citas-detail/citas-detail.component';
import { CitasDeleteComponent } from './../citas-delete/citas-delete.component';
import { CitasEditComponent } from './../citas-edit/citas-edit.component';
import { CitasAddComponent } from './../citas-add/citas-add.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-citas',
  templateUrl: './citas.component.html',
  styleUrls: ['./citas.component.scss']
})
export class CitasComponent implements OnInit {

  private loader: boolean = false;
  private allCitas: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getMyCitasNoArchivadas().subscribe(
      data => {
        this.loader = false;
        this.allCitas = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todas las citas.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(CitasEditComponent, {data: {id, isOwn: false}, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(CitasDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allCitas.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(CitasAddComponent, {width: '80%', data: {isOwn: true}});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(CitasDetailComponent, {width: '80%', data: {id}});
  }

  openArchive(id: string) {
    let modal = this.dialog.open(CitasArchivarComponent, {width: '80%', data: {id}});
    modal.afterClosed().subscribe(data => this.ngOnInit());
  }

}



