import { Component, OnInit, Inject } from '@angular/core';
import { MD_DIALOG_DATA, MdDialogRef, MdSnackBar } from '@angular/material';
import { BackendService } from 'app/backend.service';

@Component({
  selector: 'app-seg-detail',
  templateUrl: './seg-detail.component.html',
  styleUrls: ['./seg-detail.component.scss']
})
export class SegDetailComponent implements OnInit {

  private editar: boolean = false

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<SegDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar,
  ) { }

  ngOnInit() {
  }

  save(seg: string) {
    console.log(seg)
    this.back.editPaciente(this.data.id, {seg}).subscribe(data => {
      this.snack.open('Seguimiento editado correctamente.', '', {duration: 3000});
      this.editar = false
    }, err => {
      this.snack.open('Error, no se pudo editar el seguimiento.', '', {duration: 3000});
      this.ref.close()
    })
  }

  export(seg: string) {
    const printWindow = window.open('', '', 'height=400,width=800');
    printWindow.document.write(`
    <p style="white-space: pre-wrap">${seg}</p>
    `)
    printWindow.print()
  }

}



