import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-citas-edit',
  templateUrl: './citas-edit.component.html',
  styleUrls: ['./citas-edit.component.scss']
})
export class CitasEditComponent {

  private loader: boolean = false;
  private editData: any = {};
  private allUsers: any[] = [];

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CitasEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getCitaDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.editData = data;
        this.editData.paciente = data.paciente.id;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar la cita.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
    this.back.getAllPacientes().subscribe(
      data => {
        this.loader = false;
        this.allUsers = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los pacientes', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editCita(this.data.id, data).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Cita editada correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar la cita.', '', {duration: 4000});
      }
    );
  }

}



