import { UsuariosDetailComponent } from './../usuarios-detail/usuarios-detail.component';
import { UsuariosAddComponent } from './../usuarios-add/usuarios-add.component';
import { UsuariosDeleteComponent } from './../usuarios-delete/usuarios-delete.component';
import { UsuariosEditComponent } from './../usuarios-edit/usuarios-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';
import { ChangePassComponent } from 'app/change-pass/change-pass.component';

@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.scss']
})
export class UsuariosComponent implements OnInit {

  private loader: boolean = false;
  private allUsers: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllUsers().subscribe(
      data => {
        this.loader = false;
        this.allUsers = data.filter(user => user.roles.indexOf('ROLE_ADMIN') < 0);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los usuarios.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    const modal = this.dialog.open(UsuariosEditComponent, {data: id, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    const modal = this.dialog.open(UsuariosDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allUsers.splice(i, 1);
        this.ngOnInit()
      }
    );
  }

  openAdd() {
    const modal = this.dialog.open(UsuariosAddComponent, {width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allUsers.push(data);
        this.ngOnInit()
      }
    );
  }

  openDetail(id: string) {
    const modal = this.dialog.open(UsuariosDetailComponent, {width: '80%', data: {id}});
  }

  openChangePass(id: string) {
    const modal = this.dialog.open(ChangePassComponent, {width: '80%', data: {id}})
  }

}



