import { Router } from '@angular/router';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { MdSidenav } from "@angular/material";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  @ViewChild('sidenav') sidenav: MdSidenav;

  constructor(
    private back: BackendService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth < 600) this.sidenav.close();
    if (event.target.innerWidth > 600) this.sidenav.open();
  }

  isRole(role: string) {
    if(!this.back.user) return false;
    if(!this.back.user.roles) return false;
    return this.back.user.roles.indexOf(role) >= 0;
  }

  logout() {
    this.back.logout();
    this.router.navigate(['login']);
  }

}



