import { ImageResult } from 'ng2-imageupload';
import { BackendService } from './../../../backend.service';
import { MdDialogRef, MdSnackBar } from '@angular/material';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-eventos-add',
  templateUrl: './eventos-add.component.html',
  styleUrls: ['./eventos-add.component.scss']
})
export class EventosAddComponent {

  private loader: boolean = false;
  private addData: any = {};

  constructor(
    private ref: MdDialogRef<EventosAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  add(data: any) {
    this.loader = true;
    this.back.addEvent(data).subscribe(
      res => {
        this.loader = false;
        this.snack.open('Evento agregado correctamente', '', {duration: 4000});
        this.ref.close(res);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar el evento', '', {duration: 4000});
        this.ref.close({err});
      }
    );    
  }

  selected(res: ImageResult) {
    this.loader = true;
    this.back.uploadFile(res.file).subscribe(
      data => {
        this.loader = false;
        this.addData.img = data.secure_url;
        this.loader = false;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo subir la imgen', '', {duration: 4000});
      }
    );
  }

}



