import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-usuarios-detail',
  templateUrl: './usuarios-detail.component.html',
  styleUrls: ['./usuarios-detail.component.scss']
})
export class UsuariosDetailComponent implements OnInit {

  private loader: boolean = false;
  private user: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<UsuariosDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getUserDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.user = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el usuario.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



