import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-entradas-detail',
  templateUrl: './entradas-detail.component.html',
  styleUrls: ['./entradas-detail.component.scss']
})
export class EntradasDetailComponent implements OnInit {

  private loader: boolean = false;
  private event: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<EntradasDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getEntradaDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.event = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar la entrada.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



