import { ImageResult } from 'ng2-imageupload';
import { Component, OnInit, Inject } from '@angular/core';
import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-cuestionario-edit',
  templateUrl: './cuestionario-edit.component.html',
  styleUrls: ['./cuestionario-edit.component.scss']
})
export class CuestionarioEditComponent implements OnInit {

  private loader: boolean = false;
  private c: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CuestionarioEditComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) {
    
  }

  ngOnInit() {
    this.loader = true;
    this.back.getPacienteDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.c = data.cuestionario || {};
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar el cuestionario.', '', {duration: 4000});
        this.ref.close({err: err});
      }
    );
  }

  edit(data: any) {
    this.loader = true;
    this.back.editPaciente(this.data.id, {cuestionario: data}).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Cuestionario editado correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo editar el cuestionario.', '', {duration: 4000});
      }
    );
  }

  selected(res: ImageResult) {
    this.loader = true;
    this.back.uploadCurriculum(res.file).subscribe(
      data => {
        this.loader = false;
        this.c.estudio_socio = data.url;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo subir el estudio', '', {duration: 4000});
      }
    );
  }

}



