import { Component, OnInit, Inject } from '@angular/core';
import { BackendService } from 'app/backend.service';
import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';

@Component({
  selector: 'app-change-pass',
  templateUrl: './change-pass.component.html',
  styleUrls: ['./change-pass.component.scss']
})
export class ChangePassComponent implements OnInit {

  private loader: boolean = false

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<ChangePassComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
  }

  changePass(pass: string, re: string) {
    if (pass != re) return this.snack.open('Ingresa una contrasea...', '', {duration: 4000})
    
    this.back.changePass(this.data.id, pass).subscribe(data => {
      this.snack.open('Contrasea cambiada correctamente.', '', {duration: 4000})
      this.ref.close()
    }, err => {
      console.log(err)
      this.snack.open('No pudimos cambiar tu contrasea...', '', {duration: 4000})
    })

  }

}



