import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-pacientes-add',
  templateUrl: './pacientes-add.component.html',
  styleUrls: ['./pacientes-add.component.scss']
})
export class PacientesAddComponent implements OnInit {

  private loader: boolean = false;
  private addData: any = {};
  private allUsers: any[] = [];

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<PacientesAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllUsers().subscribe(
      data => {
        this.loader = false;
        this.allUsers = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los usuarios.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

  add(data: any) {
    this.loader = true;
    if(this.data.isOwn) data.terapeuta = this.back.user.id;
    data.cuestionario = {};
    this.back.addPaciente(data).subscribe(
      res => {
        this.loader = false;
        this.snack.open('Paciente agregado correctamente', '', {duration: 4000});
        this.ref.close(res);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar el paciente', '', {duration: 4000});
        this.ref.close({err});
      }
    );    
  }

}



