import { BackendService } from './../../../backend.service';
import { MdDialogRef, MdSnackBar } from '@angular/material';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-galerias-add',
  templateUrl: './galerias-add.component.html',
  styleUrls: ['./galerias-add.component.scss']
})
export class GaleriasAddComponent {

  private loader: boolean = false;
  private addData: any = {};

  constructor(
    private ref: MdDialogRef<GaleriasAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  add(data: any) {
    this.loader = true;
    data.user = this.back.user.id;
    this.back.addGaleria(data).subscribe(
      res => {
        this.loader = false;
        this.snack.open('Galera agregada correctamente', '', {duration: 4000});
        this.ref.close(res);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar la galera', '', {duration: 4000});
        this.ref.close({err});
      }
    );    
  }

}



