import { MdSnackBar, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';
import * as moment from 'moment'

@Component({
  selector: 'app-citas-add',
  templateUrl: './citas-add.component.html',
  styleUrls: ['./citas-add.component.scss']
})
export class CitasAddComponent implements OnInit {
  private loader: boolean = false;
  private addData: any = {};
  private allPacientes: any[] = [];
  private myPacientes: any[] = [];
  private allUsers: any[] = [];

  private todayDate: Date = new Date();

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CitasAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;

    this.back.getAllUsers().subscribe(data => {
      this.allUsers = data
    }, err => {
      this.snack.open('Error, no se pudieron cargar todos los terapeutas.', '', {duration: 4000});
    })
    
    if(this.data.isOwn) {
      this.back.getMyPacientes().subscribe(data => {
          this.loader = false
          this.myPacientes = data
        }, err => {
          this.loader = false
          this.snack.open('Error, no se pudieron cargar todos los pacientes.', '', {duration: 4000});
          this.ref.close({err});
        }
      )
    } else {
      this.back.getAllPacientes().subscribe(data => {
          this.loader = false;
          this.myPacientes = data;
        }, err => {
          this.loader = false;
          this.snack.open('Error, no se pudieron cargar todos los pacientes.', '', {duration: 4000});
          this.ref.close({err});
        }
      );
    }
  }

  add(data: any) {
    this.loader = true;
    if(this.data.isOwn) {
      // si soy tera
      data.tera = this.back.user.id;
    } else {
      // si soy admin
      const tera = data.paciente.terapeuta.id
      data.tera = tera
      console.log(tera, data.tera)
    }
    data.paciente = data.paciente.id
    data.archivada = false;


    this.back.citaExiste(data.hour, data.consultorio, data.date).subscribe(
      data_existe => {
        this.loader = false;
        if(data_existe.existe) {
          this.loader = false;
          this.snack.open('Error, ya existe una cita en ese consultorio a la misma hora.', '', {duration: 3000});
        } else {
          const tempDate = data.date
          delete data.date
          console.log('FINAL DATA', data)
          this.back.addCita({...data, date: moment(moment(tempDate).format('YYYY-MM-DD')).toISOString()}).subscribe(
            res => {
              console.log(res);
              
              this.loader = false;
              this.snack.open('Cita agregada correctamente', '', {duration: 4000});
              this.ref.close(res);
            },
            err => {
              this.loader = false;
              this.snack.open('Error, no se pudo agregar la cita', '', {duration: 4000});
              this.ref.close({err});
            }
          );
        }
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo validar la cita.', '', {duration: 4000});
      }
    );   
  }

}



