import { PacientesDetailComponent } from './../pacientes-detail/pacientes-detail.component';
import { PacientesAddComponent } from './../pacientes-add/pacientes-add.component';
import { PacientesDeleteComponent } from './../pacientes-delete/pacientes-delete.component';
import { PacientesEditComponent } from './../pacientes-edit/pacientes-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-pacientes',
  templateUrl: './pacientes.component.html',
  styleUrls: ['./pacientes.component.scss']
})
export class PacientesComponent implements OnInit {

  private loader: boolean = false;
  private allPacientes: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllPacientes().subscribe(
      data => {
        this.loader = false;
        this.allPacientes = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todos los pacientes.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(PacientesEditComponent, {data: {id, isOwn: false}, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(PacientesDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allPacientes.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(PacientesAddComponent, {width: '80%', data: {isOwn: false}});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allPacientes.push(data);
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(PacientesDetailComponent, {width: '80%', data: {id}});
  }

}



