import { Component, Inject } from '@angular/core';
import { MD_DIALOG_DATA, MdDialogRef, MdSnackBar } from '@angular/material';
import { BackendService } from './../../../backend.service';

@Component({
  selector: 'app-citas-archivar',
  templateUrl: './citas-archivar.component.html',
  styleUrls: ['./citas-archivar.component.scss']
})
export class CitasArchivarComponent {

  private loader: boolean = false;

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CitasArchivarComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  archivar(id: string) {
    this.loader = true;
    this.back.editCita(this.data.id, {archivada: true}).subscribe(
      data => {
        this.loader = false;
        this.snack.open('Cita archivada correctamente.', '', {duration: 4000});
        this.ref.close(data);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo archivar la cita.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



