import { MdSnackBar, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component } from '@angular/core';

@Component({
  selector: 'app-usuarios-add',
  templateUrl: './usuarios-add.component.html',
  styleUrls: ['./usuarios-add.component.scss']
})
export class UsuariosAddComponent {

  private loader: boolean = false;
  private addData: any = {};

  constructor(
    private ref: MdDialogRef<UsuariosAddComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  add(data: any) {
    this.loader = true;

    data.roles = ['ROLE_TERAPEUTA'];
    data.password = data.name.trim() + (new Date(data.bornDate).getFullYear());
    console.log('====================================');
    console.log(data.password);
    console.log('====================================');

    data.poblation = [];
    if(data.pobNinos) data.poblation.push('POB_NINOS');
    if(data.pobAdo) data.poblation.push('POB_ADO');
    if(data.pobAdultos) data.poblation.push('POB_ADULTOS');
    if(data.pobAdultosPlus) data.poblation.push('POB_ADULTOS_PLUS');
    if(data.pobFam) data.poblation.push('POB_FAM');
    if(data.pobPareja) data.poblation.push('POB_PAREJA');

    delete data.pobNinos;
    delete data.pobAdo;
    delete data.pobAdultos;
    delete data.pobAdultosPlus;
    delete data.pobFam;
    delete data.pobPareja;

    this.back.addUser(data).subscribe(
      res => {
        this.loader = false;
        this.snack.open('Usuario agregado correctamente', '', {duration: 4000});
        this.ref.close(res);
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo agregar el usuario', '', {duration: 4000});
        this.ref.close({err});
      }
    );    
  }

}



