import { GaleriasUploadComponent } from './../galerias-upload/galerias-upload.component';
import { GaleriasDetailComponent } from './../galerias-detail/galerias-detail.component';
import { GaleriasAddComponent } from './../galerias-add/galerias-add.component';
import { GaleriasDeleteComponent } from './../galerias-delete/galerias-delete.component';
import { GaleriasEditComponent } from './../galerias-edit/galerias-edit.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-galerias',
  templateUrl: './galerias.component.html',
  styleUrls: ['./galerias.component.scss']
})
export class GaleriasComponent implements OnInit {

  private loader: boolean = false;
  private allGalerias: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getAllGalerias().subscribe(
      data => {
        this.loader = false;
        this.allGalerias = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudieron cargar todas las galerias.', '', {duration: 4000});
      }
    );
  }

  openEdit(id: string, i: number) {
    let modal = this.dialog.open(GaleriasEditComponent, {data: id, width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.ngOnInit();
      }
    );
  }

  openDelete(id: string, i: number) {
    let modal = this.dialog.open(GaleriasDeleteComponent, {data: id, width: '60%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allGalerias.splice(i, 1);
      }
    );
  }

  openAdd() {
    let modal = this.dialog.open(GaleriasAddComponent, {width: '80%'});
    modal.afterClosed().subscribe(
      (data) => {
        if (!data) return;
        if (data.err) return;
        this.allGalerias.push(data);
      }
    );
  }

  openDetail(id: string) {
    let modal = this.dialog.open(GaleriasDetailComponent, {width: '80%', data: {id}});
  }

  openAddPicture(id: string) {
    let modal = this.dialog.open(GaleriasUploadComponent, {width: '80%', data: {id}});
    modal.afterClosed().subscribe(
      (data) => {
        this.ngOnInit();
      }
    );
  }

}



