import { CitasDetailComponent } from './../../terapeuta/citas-detail/citas-detail.component';
import { MdSnackBar, MdDialog } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  private loader: boolean = false;
  private allCitas: any[] = [];

  constructor(
    private back: BackendService,
    private snack: MdSnackBar,
    private dialog: MdDialog
  ) { }

  ngOnInit() {
    this.loader = true;
    if (JSON.parse(localStorage.getItem('user')).roles.indexOf('ROLE_ADMIN') >= 0) {
      this.back.getAllCitasNoArchivadas().subscribe(data => {
        this.loader = false
        this.allCitas = data
      }, err => {
        this.loader = false
        this.snack.open('Error, no se pudieron cargar todas las citas.', '', {duration: 4000});
      })
    } else if (JSON.parse(localStorage.getItem('user')).roles.indexOf('ROLE_TERAPEUTA') >= 0) {
      this.back.getMyCitasNoArchivadas().subscribe(
        data => {
          this.loader = false;
          this.allCitas = data;
        },
        err => {
          this.loader = false;
          this.snack.open('Error, no se pudieron cargar todas las citas.', '', {duration: 4000});
        }
      );
    } else {
      this.back.getCitasProximas().subscribe(
        data => {
          this.loader = false;
          this.allCitas = data;
        },
        err => {
          this.loader = false;
          this.snack.open('Error, no se pudieron cargar las prximas citas.', '', {duration: 4000});
        }
      );
    }
  }

  openDetail(id: string) {
    let modal = this.dialog.open(CitasDetailComponent, {width: '80%', data: {id}});
  }
}



